.nav{
    display:none;
}
.nav.active{
    display: flex;
    justify-content: space-around;
    align-items: center;
    padding-bottom: 1%;
    margin-top: -2%;
    flex:1 1;
    position:-webkit-sticky;
    position:sticky;
    top:0;
    z-index:100;
    color:white;
    background-color: transparent;
    box-shadow:none;
}
.nav ul{
    list-style: none;
    display: flex;
	padding-top: 1%;
}
ul li {
    margin-left: 8px;
}
nav{
    display: flex;
    justify-content: space-around;
    align-items: center;
    padding-bottom: 1%;
    margin: auto;
    flex:1 1;
    position:-webkit-sticky;
    position:sticky;
    top:0;
    z-index:100;
}
nav.active{
    background-color: #030f0f;
    z-index: 15;
    box-shadow: 0px 3px 12px -3px #fff;
    position: fixed;
    width: 100%;
    padding: 0%;
}
.navbar-container{
    display: flex;
    justify-content: space-between;
    align-items: center;
    width: 93%;
    margin: auto;
    padding: 1%;
    background: rgba(0, 0, 0, 0.4);
    border-radius: 5px;
}
.logo{
    color: red;
    z-index: 1;
    font-weight: bold;
    flex: 0.3 1;
    justify-content: flex-start;
}
.menu{
    justify-content: flex-end;
    display: flex;
    flex: 0.7 1;
    color: white;
    cursor: pointer;
    font-size: 1.5rem;
    display:none;
}

.list-nav{
    display: flex;
    list-style: none;
    font-weight: 600;
    color: white;
    flex: 0.7 1;
    justify-content: flex-end;
    padding-top: 2%;
}
.list-nav .item-nav a{
    text-decoration: none;
    margin: 0;
    margin-right: 27px;
    cursor: pointer;
    color: white;
}
.list-nav .item-nav a:hover{
    color: #b10b5e;
}
.item-nav:first-child:hover a{
    color: #8b2257;
}
.header-sidebar{
    display: none;
    color:white;
    font-size: 1.3rem;
    padding-top: 2%;
    cursor:pointer;
}
@media screen and (max-width:900px){
.nav.active{
    display:none;
}
nav{
	background-color: black;
}
.menu{
